import React from 'react';
import { useTranslation } from 'react-i18next';
import { useCarFilters } from '../../contexts/CarFilterContext';

const CarFilters = () => {
  const { t } = useTranslation();
  const { 
    filters, 
    handleFilterChange, 
    resetFilters,
    makeOptions,
    modelOptions,
    bodyTypes,
    fuelTypes 
  } = useCarFilters();

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    handleFilterChange(name, value);
  };

  return (
    <div className="bg-white p-4 rounded-lg shadow-md">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-lg font-bold text-indigo-900">{t('cars.filters.title')}</h2>
        <button 
          onClick={resetFilters}
          className="text-indigo-600 hover:underline text-sm"
        >
          {t('cars.filters.reset')}
        </button>
      </div>
      
      {/* Price Range */}
      <div className="mb-6">
        <h3 className="font-medium text-gray-700 mb-2">{t('cars.filters.priceRange')}</h3>
        <div className="grid grid-cols-2 gap-2">
          <div>
            <input
              type="number"
              name="min_price"
              placeholder={t('cars.filters.min')}
              className="w-full px-3 py-2 border rounded-lg text-sm"
              value={filters.min_price}
              onChange={handleInputChange}
            />
          </div>
          <div>
            <input
              type="number"
              name="max_price"
              placeholder={t('cars.filters.max')}
              className="w-full px-3 py-2 border rounded-lg text-sm"
              value={filters.max_price}
              onChange={handleInputChange}
            />
          </div>
        </div>
      </div>
      
      {/* Make Dropdown */}
      <div className="mb-6">
        <h3 className="font-medium text-gray-700 mb-2">{t('cars.carMake')}</h3>
        <select
          name="make"
          className="w-full px-3 py-2 border rounded-lg text-sm"
          value={filters.make}
          onChange={handleInputChange}
        >
          <option value="">{t('cars.filters.all')}</option>
          {makeOptions.map(make => (
            <option key={make} value={make}>{make}</option>
          ))}
        </select>
      </div>
      
      {/* Rest of filters... */}
      {/* ... */}
    </div>
  );
};

export default CarFilters;
