// src/pages/PrivacyPage.js
import React from 'react';
import { useTranslation } from 'react-i18next';

const PrivacyPage = () => {
  const { t } = useTranslation();

  return (
    <div className="bg-gray-50 min-h-screen">
      <div className="bg-indigo-900 text-white py-12">
        <div className="container mx-auto px-6">
          <h1 className="text-3xl font-bold">{t('footer.privacyPolicy')}</h1>
        </div>
      </div>

      <div className="container mx-auto px-6 py-12">
        <div className="bg-white rounded-lg shadow-md p-8">
          <h2 className="text-xl font-bold text-indigo-900 mb-6">
            Your Privacy is Important to Us
          </h2>
          <p className="mb-4">
            This Privacy Policy document contains types of information that is collected and recorded by MILITEX and how we use it. If you have additional questions or require more information about our Privacy Policy, please contact us.
          </p>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">Information We Collect</h2>
          <p className="mb-4">
            MILITEX collects the following information when you register on our site, place an order, subscribe to our newsletter, respond to a survey, fill out a form, or use our services:
          </p>
          <ul className="list-disc pl-6 mb-4">
            <li className="mb-2">Name and contact information including email address</li>
            <li className="mb-2">Demographic information such as postcode, preferences and interests</li>
            <li className="mb-2">Military status and verification (if provided)</li>
            <li className="mb-2">Vehicle information when selling or buying</li>
            <li className="mb-2">Payment information when making transactions</li>
            <li className="mb-2">Other information relevant to customer surveys and/or offers</li>
          </ul>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">How We Use Your Information</h2>
          <p className="mb-4">
            We use the information we collect from you for the following purposes:
          </p>
          <ul className="list-disc pl-6 mb-4">
            <li className="mb-2">To personalize your experience and to deliver the type of content and product offerings in which you are most interested</li>
            <li className="mb-2">To improve our website offerings based on the feedback we receive from you</li>
            <li className="mb-2">To allow us to better service you in responding to your customer service requests</li>
            <li className="mb-2">To administer contests, promotions, surveys or other site features</li>
            <li className="mb-2">To process transactions (we may use a third-party payment processor for this purpose)</li>
            <li className="mb-2">To verify military status when required</li>
            <li className="mb-2">To send periodic emails regarding your order or other products and services</li>
          </ul>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">Cookies</h2>
          <p className="mb-4">
            MILITEX uses cookies to store information about visitors' preferences, to record user-specific information about which pages the site visitor accesses or visits, and to personalize or customize our web page content based upon visitors' browser type or other information that the visitor sends via their browser.
          </p>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">Security</h2>
          <p className="mb-4">
            We implement a variety of security measures to maintain the safety of your personal information when you place an order, enter, submit, or access your personal information. We offer the use of a secure server. All supplied sensitive information is transmitted via Secure Socket Layer (SSL) technology and then encrypted into our payment gateway providers database only to be accessible by those authorized with special access rights to such systems, who are required to keep the information confidential.
          </p>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">Third-Party Disclosure</h2>
          <p className="mb-4">
            We do not sell, trade, or otherwise transfer to outside parties your Personally Identifiable Information unless we provide users with advance notice. This does not include website hosting partners and other parties who assist us in operating our website, conducting our business, or serving our users, so long as those parties agree to keep this information confidential. We may also release information when its release is appropriate to comply with the law, enforce our site policies, or protect ours or others' rights, property or safety.
          </p>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">Your Rights</h2>
          <p className="mb-4">
            You have the right to access, correct, or delete your personal information at any time. You can exercise these rights by accessing your account settings or contacting us directly.
          </p>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">Changes to This Privacy Policy</h2>
          <p className="mb-4">
            MILITEX reserves the right to change this Privacy Policy at any time. When we do, we will post a notification on the main page of our site and update the modification date below. We encourage users to frequently check this page for any changes to stay informed about how we are helping to protect the personal information we collect.
          </p>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">Contact Information</h2>
          <p className="mb-4">
            If you have any questions or suggestions about our Privacy Policy, do not hesitate to contact us at:
          </p>
          <p className="mb-4">
            <strong>Email:</strong> support@militex.com<br />
            <strong>Address:</strong> 123 Military Drive, Kyiv, 01001, Ukraine<br />
            <strong>Phone:</strong> +380 44 123 4567
          </p>
        </div>
      </div>
    </div>
  );
};

export default PrivacyPage;
