import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import CarCard from './CarCard';
import CarFilters from './CarFilters';
import { useCarFilters } from '../../contexts/CarFilterContext';
import CarService from '../../services/car.service';

const CarList = () => {
  const { t } = useTranslation();
  const { filters, searchTerm, setSearchTerm } = useCarFilters();
  const [cars, setCars] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [pagination, setPagination] = useState({
    count: 0,
    next: null,
    previous: null,
  });
  const [currentPage, setCurrentPage] = useState(1);
  const [showFilters, setShowFilters] = useState(false);

  // Fetch cars when filters, pagination, or search term change
  useEffect(() => {
    fetchCars();
  }, [filters, currentPage, searchTerm]);

  const fetchCars = async () => {
    try {
      setLoading(true);
      const params = {
        ...filters,
        page: currentPage,
        search: searchTerm
      };

      // Remove empty filter values
      Object.keys(params).forEach(key => 
        params[key] === '' && delete params[key]
      );

      const response = await CarService.getAllCars(params);
      setCars(response.results || []);
      setPagination({
        count: response.count || 0,
        next: response.next,
        previous: response.previous,
      });
      setError('');
    } catch (err) {
      console.error('Error fetching cars:', err);
      setError('Failed to load vehicles');
    } finally {
      setLoading(false);
    }
  };

  const handleSearchChange = (e) => {
    setSearchTerm(e.target.value);
  };

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const toggleFilters = () => {
    setShowFilters(!showFilters);
  };

  return (
    <div className="max-w-7xl mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold text-indigo-900 mb-8">{t('cars.browseVehicles')}</h1>
      
      {/* Mobile Filter Toggle */}
      <div className="md:hidden mb-4">
        <button 
          onClick={toggleFilters}
          className="flex items-center text-indigo-900 font-medium"
        >
          <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 6V4m0 2a2 2 0 100 4m0-4a2 2 0 110 4m-6 8a2 2 0 100-4m0 4a2 2 0 110-4m0 4v2m0-6V4m6 6v10m6-2a2 2 0 100-4m0 4a2 2 0 110-4m0 4v2m0-6V4" />
          </svg>
          {showFilters ? t('common.hideFilters') : t('common.showFilters')}
        </button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        {/* Filters Section */}
        <div className={`md:col-span-1 ${showFilters ? 'block' : 'hidden md:block'}`}>
          <CarFilters />
        </div>

        {/* Car List */}
        <div className="md:col-span-3">
          {/* Search Bar */}
          <div className="mb-6 flex">
            <input
              type="text"
              placeholder={t('common.search')}
              className="w-full px-4 py-2 border rounded-l-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
              onChange={handleSearchChange}
              value={searchTerm}
            />
            <button className="bg-indigo-900 text-white px-4 py-2 rounded-r-lg hover:bg-indigo-800 flex items-center justify-center">
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"></path>
              </svg>
            </button>
          </div>

          {/* Cars Grid */}
          {loading ? (
            <div className="flex justify-center py-8">
              <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-900"></div>
            </div>
          ) : error ? (
            <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded">
              {error}
            </div>
          ) : cars.length === 0 ? (
            <div className="text-center py-8 bg-gray-50 rounded-lg">
              <svg className="w-16 h-16 mx-auto text-gray-400 mb-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9.172 16.172a4 4 0 015.656 0M9 10h.01M15 10h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              <h3 className="text-lg font-medium text-gray-900 mb-1">{t('cars.noVehiclesFound')}</h3>
              <p className="text-gray-500">{t('cars.tryDifferentFilters')}</p>
            </div>
          ) : (
            <>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {cars.map(car => (
                  <CarCard key={car.id} car={car} />
                ))}
              </div>
              
              {/* Pagination */}
              {pagination.count > 0 && (
                <div className="mt-8 flex justify-between items-center">
                  <button
                    onClick={() => handlePageChange(currentPage - 1)}
                    disabled={!pagination.previous}
                    className={`px-4 py-2 rounded ${
                      pagination.previous
                        ? 'bg-indigo-900 text-white hover:bg-indigo-800'
                        : 'bg-gray-300 text-gray-500 cursor-not-allowed'
                    }`}
                  >
                    {t('common.previous')}
                  </button>
                  
                  <span className="text-gray-600">
                    {t('common.page')} {currentPage} / {Math.ceil(pagination.count / 10)}
                  </span>
                  
                  <button
                    onClick={() => handlePageChange(currentPage + 1)}
                    disabled={!pagination.next}
                    className={`px-4 py-2 rounded ${
                      pagination.next
                        ? 'bg-indigo-900 text-white hover:bg-indigo-800'
                        : 'bg-gray-300 text-gray-500 cursor-not-allowed'
                    }`}
                  >
                    {t('common.next')}
                  </button>
                </div>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default CarList;