// src/pages/CookiesPage.js
import React from 'react';
import { useTranslation } from 'react-i18next';

const CookiesPage = () => {
  const { t } = useTranslation();

  return (
    <div className="bg-gray-50 min-h-screen">
      <div className="bg-indigo-900 text-white py-12">
        <div className="container mx-auto px-6">
          <h1 className="text-3xl font-bold">{t('footer.cookiePolicy')}</h1>
        </div>
      </div>

      <div className="container mx-auto px-6 py-12">
        <div className="bg-white rounded-lg shadow-md p-8">
          <h2 className="text-xl font-bold text-indigo-900 mb-6">
            What Are Cookies
          </h2>
          <p className="mb-4">
            As is common practice with almost all professional websites, MILITEX uses cookies, which are tiny files that are downloaded to your computer, to improve your experience. This page describes what information they gather, how we use it and why we sometimes need to store these cookies. We will also share how you can prevent these cookies from being stored, however this may downgrade or 'break' certain elements of the site's functionality.
          </p>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">How We Use Cookies</h2>
          <p className="mb-4">
            We use cookies for a variety of reasons detailed below. Unfortunately, in most cases there are no industry standard options for disabling cookies without completely disabling the functionality and features they add to this site. It is recommended that you leave on all cookies if you are not sure whether you need them or not in case they are used to provide a service that you use.
          </p>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">The Cookies We Set</h2>

          <h3 className="text-lg font-semibold text-indigo-800 mb-3">Account related cookies</h3>
          <p className="mb-4">
            If you create an account with us, we will use cookies for the management of the signup process and general administration. These cookies will usually be deleted when you log out, however in some cases they may remain to remember your site preferences when logged out.
          </p>

          <h3 className="text-lg font-semibold text-indigo-800 mb-3">Login related cookies</h3>
          <p className="mb-4">
            We use cookies when you are logged in so that we can remember this fact. This prevents you from having to log in every single time you visit a new page. These cookies are typically removed or cleared when you log out to ensure that you can only access restricted features and areas when logged in.
          </p>

          <h3 className="text-lg font-semibold text-indigo-800 mb-3">Forms related cookies</h3>
          <p className="mb-4">
            When you submit data through a form such as those found on contact pages or comment forms, cookies may be set to remember your user details for future correspondence.
          </p>

          <h3 className="text-lg font-semibold text-indigo-800 mb-3">Site preferences cookies</h3>
          <p className="mb-4">
            In order to provide you with a great experience on this site, we provide the functionality to set your preferences for how this site runs when you use it. In order to remember your preferences, we need to set cookies so that this information can be called whenever you interact with a page that is affected by your preferences.
          </p>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">Third Party Cookies</h2>
          <p className="mb-4">
            In some special cases we also use cookies provided by trusted third parties. The following section details which third party cookies you might encounter through this site.
          </p>
          <ul className="list-disc pl-6 mb-4">
            <li className="mb-2">This site uses Google Analytics which is one of the most widespread and trusted analytics solutions on the web for helping us to understand how you use the site and ways that we can improve your experience. These cookies may track things such as how long you spend on the site and the pages that you visit so we can continue to produce engaging content.</li>
            <li className="mb-2">From time to time we test new features and make subtle changes to the way that the site is delivered. When we are still testing new features, these cookies may be used to ensure that you receive a consistent experience whilst on the site whilst ensuring we understand which optimizations our users appreciate the most.</li>
            <li className="mb-2">We also use social media buttons and/or plugins on this site that allow you to connect with your social network in various ways. For these to work, social media sites including Facebook, Twitter, and others, will set cookies through our site, which may be used to enhance your profile on their site or contribute to the data they hold for various purposes outlined in their respective privacy policies.</li>
          </ul>

          <h2 className="text-xl font-bold text-indigo-900 mb-6 mt-8">More Information</h2>
          <p className="mb-4">
            Hopefully, this has clarified things for you. As mentioned earlier, if there is something that you aren't sure whether you need or not, it's usually safer to leave cookies enabled in case it does interact with one of the features you use on our site.
          </p>
          <p className="mb-4">
            However, if you are still looking for more information, then you can contact us through one of our preferred contact methods:
          </p>
          <p className="mb-4">
            <strong>Email:</strong> support@militex.com<br />
            <strong>Phone:</strong> +380 44 123 4567
          </p>
        </div>
      </div>
    </div>
  );
};

export default CookiesPage;