// src/pages/FAQPage.js
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';

const FAQPage = () => {
  const { t } = useTranslation();

  // State to track which FAQ items are expanded
  const [expandedItems, setExpandedItems] = useState({});

  // Toggle expanded state for an FAQ item
  const toggleItem = (id) => {
    setExpandedItems(prev => ({
      ...prev,
      [id]: !prev[id]
    }));
  };

  // FAQ data organized by categories
  const faqData = [
    {
      category: "General",
      items: [
        {
          id: "what-is-militex",
          question: "What is MILITEX?",
          answer: "MILITEX is a specialized vehicle marketplace dedicated to supporting military personnel, veterans, and their families. We provide a platform for buying and selling vehicles with features tailored to the unique needs of the military community."
        },
        {
          id: "who-can-use",
          question: "Who can use MILITEX?",
          answer: "MILITEX is open to everyone, but we offer special features and benefits for verified military personnel, veterans, and their immediate family members. Non-military users can also browse and purchase vehicles on our platform."
        },
        {
          id: "military-verification",
          question: "How does military verification work?",
          answer: "Upon registration, you can indicate your military status. To become verified, you'll need to provide your military ID or other acceptable documentation. Our team will review your submission and update your account status accordingly. Verified members may access special features and listings."
        }
      ]
    },
    {
      category: "Account & Registration",
      items: [
        {
          id: "create-account",
          question: "How do I create an account?",
          answer: "To create an account, click on the 'Sign Up' button in the top-right corner of the page. Fill out the registration form with your personal information, create a password, and indicate whether you're military personnel. You'll receive a confirmation email to activate your account."
        },
        {
          id: "edit-profile",
          question: "How can I edit my profile information?",
          answer: "Once logged in, click on your username in the top-right corner and select 'Profile' from the dropdown menu. On your profile page, click the 'Edit Profile' button to update your personal information, contact details, and account settings."
        },
        {
          id: "forgot-password",
          question: "I forgot my password. How can I reset it?",
          answer: "On the login page, click the 'Forgot Password' link below the login form. Enter the email address associated with your account, and we'll send you instructions to reset your password. Check your spam folder if you don't receive the email within a few minutes."
        }
      ]
    },
    {
      category: "Buying Vehicles",
      items: [
        {
          id: "search-vehicles",
          question: "How do I search for vehicles?",
          answer: "Use the 'Buy' navigation option to access our vehicle listings. You can filter vehicles by make, model, price range, year, mileage, and other specifications. Use the search bar to find specific vehicles by keywords."
        },
        {
          id: "contact-seller",
          question: "How do I contact a seller?",
          answer: "When viewing a vehicle listing, click the 'Contact Seller' button to see the seller's contact information. For imported listings, you'll be redirected to the original listing site. For direct listings, you'll see the seller's phone number or email address to initiate contact."
        },
        {
          id: "safe-buying",
          question: "How can I ensure a safe buying experience?",
          answer: "We recommend arranging in-person meetings in safe, public locations during daylight hours. Always inspect the vehicle thoroughly and request service records. Consider having a trusted mechanic examine the vehicle before purchase. For payments, avoid wire transfers and cash when possible; instead, use secure payment methods like bank transfers or escrow services for high-value transactions."
        }
      ]
    },
    {
      category: "Selling Vehicles",
      items: [
        {
          id: "create-listing",
          question: "How do I create a vehicle listing?",
          answer: "Log in to your account, go to the 'Sell' page, and fill out the vehicle details form. Include comprehensive information about your vehicle including make, model, year, mileage, condition, and price. Upload clear photos of your vehicle from multiple angles. Once submitted, your listing will be reviewed and published."
        },
        {
          id: "listing-fees",
          question: "Are there fees for listing a vehicle?",
          answer: "Basic listings are currently free for all users. Verified military personnel receive additional promotional features at no cost. In the future, we may introduce premium listing options with enhanced visibility and promotional benefits."
        },
        {
          id: "edit-listing",
          question: "How can I edit or remove my listing?",
          answer: "Go to your profile page and select 'My Vehicles'. Find the listing you want to modify and click 'Edit' to update details or 'Delete' to remove it. You can also mark your vehicle as sold to prevent further inquiries."
        }
      ]
    },
    {
      category: "Fundraising",
      items: [
        {
          id: "fundraiser-purpose",
          question: "What is the purpose of the fundraising feature?",
          answer: "Our fundraising feature allows military community members to create campaigns for various purposes, such as supporting injured veterans, assisting with emergency vehicle repairs, or helping with relocation expenses. This feature strengthens our community support network."
        },
        {
          id: "start-fundraiser",
          question: "How do I start a fundraiser?",
          answer: "Navigate to the 'Fundraiser' section, click 'Start Fundraiser', and complete the fundraiser creation form. Provide a compelling title, description, target amount, and optionally upload an image. Your fundraiser will be reviewed before being published on the platform."
        },
        {
          id: "donate",
          question: "How can I donate to a fundraiser?",
          answer: "Browse active fundraisers in the 'Fundraiser' section. When you find a cause you'd like to support, click the 'Donate' button on that fundraiser's page. Enter your donation amount, add an optional message, and complete the payment process. You can choose to donate anonymously if preferred."
        }
      ]
    }
  ];

  return (
    <div className="bg-gray-50 min-h-screen">
      <div className="bg-indigo-900 text-white py-12">
        <div className="container mx-auto px-6">
          <h1 className="text-3xl font-bold">{t('footer.faq')}</h1>
        </div>
      </div>

      <div className="container mx-auto px-6 py-12">
        <div className="bg-white rounded-lg shadow-md p-8">
          <div className="mb-12">
            <h2 className="text-2xl font-bold text-indigo-900 mb-4">Frequently Asked Questions</h2>
            <p className="text-gray-600">
              Find answers to commonly asked questions about MILITEX, our services, and how to make the most of our platform.
              If you can't find the information you're looking for, please don't hesitate to <a href="/contact" className="text-indigo-600 hover:underline">contact us</a>.
            </p>
          </div>

          {/* FAQ Categories and Items */}
          <div className="space-y-10">
            {faqData.map((category) => (
              <div key={category.category} className="border-b border-gray-200 pb-6 last:border-b-0 last:pb-0">
                <h3 className="text-xl font-bold text-indigo-900 mb-6">{category.category}</h3>
                <div className="space-y-4">
                  {category.items.map((item) => (
                    <div
                      key={item.id}
                      className="border border-gray-200 rounded-lg overflow-hidden"
                    >
                      <button
                        className="w-full text-left px-6 py-4 flex justify-between items-center bg-gray-50 hover:bg-gray-100 transition-colors"
                        onClick={() => toggleItem(item.id)}
                        aria-expanded={expandedItems[item.id]}
                      >
                        <span className="font-medium text-gray-900">{item.question}</span>
                        <svg
                          className={`w-5 h-5 text-gray-500 transform transition-transform ${expandedItems[item.id] ? 'rotate-180' : ''}`}
                          fill="none"
                          stroke="currentColor"
                          viewBox="0 0 24 24"
                        >
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 9l-7 7-7-7" />
                        </svg>
                      </button>

                      {expandedItems[item.id] && (
                        <div className="px-6 py-4 bg-white">
                          <p className="text-gray-600">{item.answer}</p>
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              </div>
            ))}
          </div>

          {/* Contact CTA */}
          <div className="mt-12 bg-indigo-50 p-6 rounded-lg">
            <h3 className="text-lg font-semibold text-indigo-900 mb-2">Still have questions?</h3>
            <p className="text-gray-600 mb-4">
              If you couldn't find the answer to your question, our support team is ready to help!
            </p>
            <a
              href="/contact"
              className="inline-block px-6 py-3 bg-indigo-900 text-white font-medium rounded-lg hover:bg-indigo-800 transition-colors"
            >
              Contact Us
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FAQPage;